.cart__btn
{
    
    background: var(--link-bg);
    box-shadow: 0 0px 20px rgb(211, 209, 209);
    padding: 1rem 1.2rem;
    border-radius: 20px;
    border-bottom-right-radius: 50px;
    color: #ffffff;
    font-weight: 500;
    position: absolute;
    bottom: -5%;
    right: 0%;
    display: flex;
    align-items: center;
    transition: transform 0.3s ease-out;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border: none;
    outline: none;
    transition: all 1s;
    font-size: 0.8rem;
}
.cart__btn:hover{
    color: #fff;
    text-decoration: none;
    transform: scale(1.02);
    box-shadow: 0px 0px 30px 1px #00000073;
    background: var(--btn-gradient-bg);
    border-radius: 20px;
    border-top-left-radius: 50px;
    transition: all 1s;
}
.cart__btn> span{
    font-size: 0.8rem;
    display: none;
    transition: all 1s;
}
.cart__btn:hover > span {
    display: inline;
    margin-right: 10px;
}

